import { Action } from '@ali/4ever-cangjie';

export const TABLE_INSERT_TABLE = Symbol('TABLE_INSERT_TABLE');

export function insertTable(rowSize, colSize, containerWidth?: number | null) {
  return {
    type: TABLE_INSERT_TABLE,
    payload: {
      rowSize,
      colSize,
      containerWidth,
    },
  };
}

interface TableInsertTablePayload {
  rowSize: number;
  colSize: number;
  containerWidth?: number;
}

interface TableInsertTableAction {
  type: typeof TABLE_INSERT_TABLE;
  payload: TableInsertTablePayload;
}

export function isTableInsertTableAction(
  action: Action,
): action is TableInsertTableAction {
  return action.type === TABLE_INSERT_TABLE;
}
